html,
body,
#root {
  width: 100%;
  height: 100%;
}

#root {
  display: flex;
  justify-content: center;
}

* {
  box-sizing: border-box ;
}

.mainText {
  position: relative;
  display: inline-block;
  margin-top: 15px;
  &:after {
    content: "";
    position: absolute;
    background-color: #000;
    height: 2px;
    width: 100%;
    bottom: -3px;
    left: 0;
  }
}
